import React from 'react';
import { DndContext, closestCenter } from '@dnd-kit/core';
import { SortableContext, verticalListSortingStrategy, useSortable } from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';
import SortableVendorField from './SortableVendorField';

// Draggable Section Header Component
function DraggableSectionHeader({ children, id }) {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
  } = useSortable({ id });

  const style = {
    transform: CSS.Transform.toString(transform),
    transition,
  };

  return (
    <div 
      ref={setNodeRef} 
      style={style} 
      className="section-header"
      {...attributes} 
      {...listeners} 
      title="Drag to reorder sections"
    >
      {children}
      <span className="drag-indicator" style={{ 
        float: 'right', 
        opacity: 1,
        fontSize: '12px',
        marginRight: '8px',
        color: '#7c3aed'
      }}>
        ::
      </span>
    </div>
  );
}

function Section3Vendor({ 
  vendorFields, 
  sensors, 
  onVendorDragEnd,
  onAddVendorField,
  onRemoveVendorField,
  onLabelChange,
  onContentChange,
  lastModified
}) {
  // Handler for adding new vendor fields
  const handleAddField = () => {
    const newField = {
      id: `vendor-field-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
      label: 'New Field:',
      placeholder: 'Enter field value'
    };
    onAddVendorField(newField);
  };

  // Log field changes to console for tracking
  React.useEffect(() => {
    // Only log when there are actual changes, not on initial render
    if (vendorFields.length > 0) {
      console.log('🔄 Vendor Fields Updated:', {
        totalFields: vendorFields.length,
        lastModified: new Date(lastModified || Date.now()).toLocaleString(),
        fields: vendorFields.map((field, index) => ({
          position: index + 1,
          id: field.id,
          label: field.label,
          placeholder: field.placeholder
        }))
      });
    }
  }, [vendorFields, lastModified]);

  return (
    <div className="vendor-column">
      {/* Section Header with Drag Handle */}
      <DraggableSectionHeader id="section3">
        Section 3 - VENDOR
      </DraggableSectionHeader>
      
      {/* Vendor Fields with Drag and Drop */}
      <DndContext 
        sensors={sensors}
        collisionDetection={closestCenter}
        onDragEnd={onVendorDragEnd}
      >
        <SortableContext 
          items={vendorFields.map(field => field.id)}
          strategy={verticalListSortingStrategy}
        >
          <div className="vendor-fields-container">
            {vendorFields.map(field => (
              <SortableVendorField 
                key={field.id} 
                field={field}
                onRemove={() => onRemoveVendorField(field.id)}
                onLabelChange={(newLabel) => onLabelChange(field.id, newLabel)}
                onContentChange={(newContent) => onContentChange(field.id, newContent, 'vendor')}
              />
            ))}
          </div>
        </SortableContext>
      </DndContext>

      {/* Add Field Button */}
      <div className="add-field-section">
        <button 
          className="add-field-btn" 
          onClick={handleAddField}
          title="Add new vendor field"
        >
          +
        </button>
      </div>
    </div>
  );
}

export default Section3Vendor;
