import React from 'react';
import { DndContext, closestCenter } from '@dnd-kit/core';
import { SortableContext, verticalListSortingStrategy, useSortable } from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';
import SortableShipToField from './SortableShipToField';

// Draggable Section Header Component
function DraggableSectionHeader({ children, id }) {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
  } = useSortable({ id });

  const style = {
    transform: CSS.Transform.toString(transform),
    transition,
  };

  return (
    <div 
      ref={setNodeRef} 
      style={style} 
      className="section-header"
      {...attributes} 
      {...listeners} 
      title="Drag to reorder sections"
    >
      {children}
      <span className="drag-indicator" style={{ 
        float: 'right', 
        opacity: 1,
        fontSize: '12px',
        marginRight: '8px',
        color: '#7c3aed'
      }}>
        ::
      </span>
    </div>
  );
}

function Section4ShipTo({ 
  shipToFields, 
  sensors, 
  onShipToDragEnd,
  onAddShipToField,
  onRemoveShipToField,
  onLabelChange,
  onContentChange,
  lastModified
}) {
  // Handler for adding new ship-to fields
  const handleAddField = () => {
    const newField = {
      id: `ship-to-field-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
      label: 'New Field:',
      placeholder: 'Enter field value'
    };
    onAddShipToField(newField);
  };

  // Log field changes to console for tracking
  React.useEffect(() => {
    // Only log when there are actual changes, not on initial render
    if (shipToFields.length > 0) {
      console.log('🔄 Ship-To Fields Updated:', {
        totalFields: shipToFields.length,
        lastModified: new Date(lastModified || Date.now()).toLocaleString(),
        fields: shipToFields.map((field, index) => ({
          position: index + 1,
          id: field.id,
          label: field.label,
          placeholder: field.placeholder
        }))
      });
    }
  }, [shipToFields, lastModified]);

  return (
    <div className="ship-to-column">
      {/* Section Header with Drag Handle */}
      <DraggableSectionHeader id="section4">
        Section 4 - SHIP TO
      </DraggableSectionHeader>
      
      {/* Ship-To Fields with Drag and Drop */}
      <DndContext 
        sensors={sensors}
        collisionDetection={closestCenter}
        onDragEnd={onShipToDragEnd}
      >
        <SortableContext 
          items={shipToFields.map(field => field.id)}
          strategy={verticalListSortingStrategy}
        >
          <div className="ship-to-fields-container">
            {shipToFields.map(field => (
              <SortableShipToField 
                key={field.id} 
                field={field}
                onRemove={() => onRemoveShipToField(field.id)}
                onLabelChange={(newLabel) => onLabelChange(field.id, newLabel)}
                onContentChange={(newContent) => onContentChange(field.id, newContent, 'ship-to')}
              />
            ))}
          </div>
        </SortableContext>
      </DndContext>

      {/* Add Field Button */}
      <div className="add-field-section">
        <button 
          className="add-field-btn" 
          onClick={handleAddField}
          title="Add new ship-to field"
        >
          +
        </button>
      </div>
    </div>
  );
}

export default Section4ShipTo;
