// Draggable Section Wrapper Component for Sections
function DraggableSectionWrapper({ children, id, sectionNumber, isSectionHandleDragging = false, showDragHandle = true }) {

  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging
  } = useSortable({ 
    id, 
    data: {
      type: 'section',
      sectionNumber: sectionNumber
    }
  });

  // Map section numbers to proper CSS classes
  const getSectionClass = (sectionNum) => {
    switch(sectionNum) {
      case 1:
        return 'section-1-company-info';
      case 2:
        return 'section-2-purchase-order';
      default:
        return `section-${sectionNum}`;
    }
  };

  const style = {
    transform: CSS.Transform.toString(transform),
    transition,
    opacity: isDragging ? 0.6 : 1,
    zIndex: isDragging ? 1000 : 1,
    cursor: isDragging ? 'grabbing' : 'grab',
    position: 'relative',
    width: '100%',
    height: '100%',
    border: isDragging ? '3px solid #ff6b6b' : '2px solid #ddd',
    borderRadius: '8px',
    backgroundColor: isDragging ? 'rgba(255, 107, 107, 0.1)' : 'transparent'
  };

  // Log the actual attributes and listeners objects
  console.log(`🔧 Detailed useSortable data for ${id}:`, {
    attributes: attributes,
    listeners: listeners,
    setNodeRef: setNodeRef,
    style: style
  });

  // Debug: Check if listeners have the right properties
  if (listeners) {
    console.log(`🔧 Listeners for ${id}:`, {
      hasOnMouseDown: !!listeners.onMouseDown,
      hasOnMouseMove: !!listeners.onMouseMove,
      hasOnMouseUp: !!listeners.onMouseUp,
      hasOnTouchStart: !!listeners.onTouchStart,
      hasOnTouchMove: !!listeners.onTouchMove,
      hasOnTouchEnd: !!listeners.onTouchEnd,
      listenerKeys: Object.keys(listeners),
      listenerValues: Object.values(listeners).map(v => typeof v)
    });
  } else {
    console.log(`❌ No listeners for ${id}`);
  }

  return (
    <div 
      ref={setNodeRef} 
      style={style} 
      className={`sortable-section ${getSectionClass(sectionNumber)} ${isDragging ? 'dragging' : ''}`}
      data-section={sectionNumber}
      data-testid={`draggable-section-${id}`}
      data-dragging={isDragging}
      {...attributes} 
      {...listeners}
      onClick={() => console.log(`🖱️ Clicked on section ${id}`)}
    >
      <div 
        className="section-content"
        style={{ position: 'relative', width: '100%', height: '100%' }}
      >
        {children}
      </div>
    </div>
  );
}



// Sortable shipping column header component for shipping details
function SortableShippingColumnHeader({ children, id }) {
  const { attributes, listeners, setNodeRef, transform, transition, isDragging } = useSortable({ id });
  
  const style = {
    transform: CSS.Transform.toString(transform),
    transition,
    cursor: 'grab',
    userSelect: 'none'
  };

  return (
    <div 
      ref={setNodeRef} 
      style={style} 
      {...attributes} 
      {...listeners}
      className={`sortable-column-header ${isDragging ? 'dragging' : ''}`}
    >
      {children}
    </div>
  );
}

